.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
body {
  padding: 16px;
  font-family: "Zain";
  background-color: rgba(214, 196, 161, 0.5);
  color: #4a4032;
}
html{
  scroll-behavior: smooth;
}
@font-face {
  font-family: "Zain";
  src: url("/public/Zain.ttf") format("truetype");
  font-weight: 700;
  font-style: normal;
}